import moment from 'moment';
import 'moment/locale/es';
import { useEffect, useState } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { useDispatch, useSelector } from 'react-redux';
import { eventSetActive, eventStartLoading } from '../../actions/eventActions';
import { openModal } from '../../actions/uiAction';
import { messages } from '../../helpers/calendarMsg';
import CalendarModal from '../calendarModal/CalendarModal';
import AddNewFab from '../ui/AddNewFab.js/AddNewFab';
import DeleteEventFab from '../ui/DeleteEventFab.js/DeleteEventFab';
import Navbar from '../ui/navbar/Navbar';
import CalendarEvent from './CalendarEvent';

moment.locale('es');

const localizer = momentLocalizer(moment);

const CalendarScreen = () => {
  const dispatch = useDispatch();
  const { events, activeEvent } = useSelector((state) => state.event);
  const { uid, name } = useSelector((state) => state.auth);

  const [lastView, setLastView] = useState(
    localStorage.getItem('lastView') || 'month'
  );

  useEffect(() => {
    dispatch(eventStartLoading());
  }, [dispatch]);

  const onDoubleClick = (e) => {
    dispatch(openModal());
  };

  const onSelectEvent = (e) => {
    dispatch(eventSetActive(e));
  };

  const onViewChange = (e) => {
    setLastView(e);
    localStorage.setItem('lastView', e);
  };

  const onSelecteSlot = (e) => {
    console.log(e);
    const slotEvent = {
      title: '',
      start: moment(e.start),
      end: moment(e.end),
      user: {
        name,
        _id: uid,
      },
    };
    dispatch(eventSetActive(slotEvent));
    dispatch(openModal());
  };

  const eventStyleGetter = (event, start, end, isSelected) => {
    const style = {
      backgroundColor: uid === event.user._id ? '#367cf7' : '#465660',
      borderRadius: '0px',
      opacity: 0.8,
      display: 'block',
      color: 'white',
    };

    return {
      style,
    };
  };

  return (
    <div className='calendar-screen'>
      <Navbar />
      <Calendar
        localizer={localizer}
        events={events}
        startAccessor='start'
        endAccessor='end'
        messages={messages}
        eventPropGetter={eventStyleGetter}
        components={{
          event: CalendarEvent,
        }}
        onSelectSlot={onSelecteSlot}
        selectable={true}
        onDoubleClickEvent={onDoubleClick}
        onSelectEvent={onSelectEvent}
        onView={onViewChange}
        view={lastView}
      />

      <AddNewFab />
      {activeEvent && activeEvent.id && <DeleteEventFab />}
      <CalendarModal />
    </div>
  );
};

export default CalendarScreen;
