import React from 'react';
import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';
import { startLogin, startRegister } from '../../actions/authAction';
import useForm from '../../hooks/useForm';
import './login.css';

const LoginScreen = () => {
  const dispatch = useDispatch();

  const [formLoginValues, handleLoginInputForm] = useForm({
    lemail: 'carlos@mail.com',
    lpassword: '123456',
  });

  const { lemail, lpassword } = formLoginValues;

  const handleLogin = (e) => {
    e.preventDefault();
    dispatch(startLogin(lemail, lpassword));
  };

  const [formRegisterValues, handleRegisterInputForm] = useForm({
    remail: 'javier@mail.com',
    rpassword: '123456',
    rpassword2: '123456',
    rname: 'javier',
  });

  const { remail, rpassword, rpassword2, rname } = formRegisterValues;

  const handleRegister = (e) => {
    e.preventDefault();
    if (rpassword !== rpassword2) {
      return Swal.fire('Error', 'The passwords dont match', 'error');
    }
    dispatch(startRegister(remail, rpassword, rname));
  };

  return (
    <div className='container login-container'>
      <div className='row ' style={{ width: '100%' }}>
        <div className='col-md-6 col-lg-6 login-form-1'>
          <h3>Ingreso</h3>
          <form onSubmit={handleLogin}>
            <div className='form-group m-2'>
              <input
                type='text'
                className='form-control'
                placeholder='Correo'
                name='lemail'
                value={lemail}
                onChange={handleLoginInputForm}
              />
            </div>
            <div className='form-group m-2'>
              <input
                type='password'
                className='form-control'
                placeholder='Contraseña'
                name='lpassword'
                value={lpassword}
                onChange={handleLoginInputForm}
              />
            </div>
            <div className='form-group m-2'>
              <input type='submit' className='btnSubmit' value='Login' />
            </div>
          </form>
        </div>

        <div className='col-md-6 col-lg-6 login-form-2'>
          <h3>Registro</h3>
          <form onSubmit={handleRegister}>
            <div className='form-group m-2'>
              <input
                type='text'
                className='form-control'
                placeholder='Nombre'
                name='rname'
                value={rname}
                onChange={handleRegisterInputForm}
              />
            </div>
            <div className='form-group m-2'>
              <input
                type='email'
                className='form-control'
                placeholder='Correo'
                name='remail'
                value={remail}
                onChange={handleRegisterInputForm}
              />
            </div>
            <div className='form-group m-2'>
              <input
                type='password'
                className='form-control'
                placeholder='Contraseña'
                name='rpassword'
                value={rpassword}
                onChange={handleRegisterInputForm}
              />
            </div>

            <div className='form-group m-2'>
              <input
                type='password'
                className='form-control'
                placeholder='Repita la contraseña'
                name='rpassword2'
                value={rpassword2}
                onChange={handleRegisterInputForm}
              />
            </div>

            <div className='form-group m-2'>
              <input type='submit' className='btnSubmit' value='Crear cuenta' />
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default LoginScreen;
