import Swal from 'sweetalert2';
import { fetchConToken } from '../helpers/fetch';
import { mapEventData } from '../helpers/mapEventData';
import { types } from '../types/types';

export const eventStarAddNew = (event) => {
  return async (dispatch, getState) => {
    const { uid, name } = getState().auth;
    try {
      const resp = await fetchConToken('events/', event, 'POST');
      const body = await resp.json();
      if (body.ok) {
        event.id = body.event.uid;
        event.user = {
          _id: uid,
          name,
        };
        dispatch(eventAddNew(event));
      }
    } catch (error) {
      console.log(error);
    }
  };
};

export const eventStartUpdated = (event) => {
  return async (dispatch) => {
    try {
      const resp = await fetchConToken(`events/${event.id}`, event, 'PUT');
      const body = await resp.json();
      if (body.ok) {
        dispatch(eventUpdated(event));
      } else {
        Swal.fire('Error', body.msg, 'error');
      }
    } catch (error) {
      console.log(error);
    }
  };
};

export const eventStartDelete = (event) => {
  return async (dispatch, getState) => {
    try {
      const { id } = getState().event.activeEvent;
      const resp = await fetchConToken(`events/${id}`, {}, 'DELETE');
      const body = await resp.json();
      if (body.ok) {
        dispatch(eventDelete(event));
      } else {
        Swal.fire('Error', body.msg, 'error');
      }
    } catch (error) {
      console.log(error);
    }
  };
};

const eventAddNew = (event) => ({
  type: types.eventAddNew,
  payload: event,
});

export const eventSetActive = (event) => ({
  type: types.eventSetActive,
  payload: event,
});

export const eventClearActiveEvent = () => ({
  type: types.eventClearActiveEvent,
});

const eventUpdated = (event) => ({
  type: types.eventUpdate,
  payload: event,
});

const eventDelete = () => ({
  type: types.eventDelete,
});

export const eventStartLoading = () => {
  return async (dispatch) => {
    try {
      const resp = await fetchConToken('events/');
      const body = await resp.json();
      const events = body.events;
      if (body.ok) {
        dispatch(eventLoader(mapEventData(events)));
      }
    } catch (error) {
      console.log(error);
    }
  };
};

const eventLoader = (events) => ({
  type: types.eventLoader,
  payload: events,
});

export const eventClearAfterLogout = () => ({
  type: types.eventClearAll,
});
