import moment from 'moment';
import { useEffect, useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import Swel from 'sweetalert2';
import {
  eventClearActiveEvent,
  eventStarAddNew,
  eventStartUpdated,
} from '../../actions/eventActions';
import { closeModal } from '../../actions/uiAction';
import { customStyles } from '../../helpers/centerModal';
import './calendarModal.css';

Modal.setAppElement('#root');

const startDate = moment().minutes(0).seconds(0).add(1, 'hours');
const endDate = startDate.clone().add(1, 'hour');

const initEvent = {
  title: '',
  notes: '',
  start: startDate.toDate(),
  end: endDate.toDate(),
};

const CalendarModal = () => {
  const [dateStart, setDateStart] = useState(startDate.toDate());
  const [dateEnd, setDateEnd] = useState(endDate.toDate());
  const [titleValid, setTitleValid] = useState(true);

  const { modalOpen } = useSelector((state) => state.ui);
  const { activeEvent } = useSelector((state) => state.event);

  const isActiveEvent = activeEvent?.id ? true : false;

  const dispatch = useDispatch();

  const [formValues, setFormValues] = useState(initEvent);

  const { notes, title, start, end } = formValues;

  const handleCloseModal = () => {
    dispatch(closeModal());
    dispatch(eventClearActiveEvent());
    setFormValues(initEvent);
  };

  useEffect(() => {
    if (activeEvent) {
      setFormValues(activeEvent);
    } else {
      setFormValues(initEvent);
    }
  }, [activeEvent]);

  const handleInputChange = ({ target }) => {
    setFormValues({
      ...formValues,
      [target.name]: target.value,
    });
  };

  const handleStartDateChange = (e) => {
    setDateStart(e);
    setFormValues({
      ...formValues,
      start: moment(e).toDate(),
    });
  };

  const handleEndDateChange = (e) => {
    setDateEnd(e);
    setFormValues({
      ...formValues,
      end: moment(e).toDate(),
    });
  };

  const handleSubmitForm = (e) => {
    e.preventDefault();

    const momentStart = moment(start);
    const momentEnd = moment(end);

    if (momentStart.isSameOrAfter(momentEnd)) {
      Swel.fire({
        title: 'Error',
        text: 'La fecha de fin debe ser mayor a la fecha de inicio',
        icon: 'error',
      });
      return;
    }

    if (title.trim().length < 2) {
      return setTitleValid(false);
    }

    if (isActiveEvent) {
      dispatch(eventStartUpdated(formValues));
    } else {
      dispatch(eventStarAddNew(formValues));
    }

    setTitleValid(true);
    handleCloseModal();
  };

  return (
    <Modal
      isOpen={modalOpen}
      onRequestClose={handleCloseModal}
      style={customStyles}
      className='modal'
      closeTimeoutMS={200}
      overlayClassName='modal-fondo'>
      <h1> {isActiveEvent ? 'Actualizar evento' : 'Nuevo evento'}</h1>
      <hr />
      <form className='container' onSubmit={handleSubmitForm}>
        <div className='form-group'>
          <label>Fecha y hora inicio</label>
          <DateTimePicker
            className='form-control'
            onChange={handleStartDateChange}
            value={dateStart}
          />
        </div>

        <div className='form-group'>
          <label>Fecha y hora fin</label>
          <DateTimePicker
            className='form-control'
            onChange={handleEndDateChange}
            value={dateEnd}
            minDate={dateStart}
          />
        </div>

        <hr />
        <div className='form-group'>
          <label>Titulo y notas</label>
          <input
            type='text'
            className={`form-control ${!titleValid && 'is-invalid'}`}
            placeholder='Título del evento'
            name='title'
            autoComplete='off'
            onChange={handleInputChange}
            value={title}
          />
          <small id='emailHelp' className='form-text text-muted'>
            Una descripción corta
          </small>
        </div>

        <div className='form-group'>
          <textarea
            type='text'
            className='form-control'
            placeholder='Notas'
            rows='5'
            name='notes'
            onChange={handleInputChange}
            value={notes}></textarea>
          <small id='emailHelp' className='form-text text-muted'>
            Información adicional
          </small>
        </div>

        <div className='d-grid'>
          <button type='submit' className='btn btn-outline-primary btn-block'>
            <i className='far fa-save'></i>
            <span> {isActiveEvent ? 'Actualizar evento' : 'Nuevo evento'}</span>
          </button>
        </div>
      </form>
    </Modal>
  );
};

export default CalendarModal;
